.radio-block__label {
  position: relative;
  padding-left: 62px;
  display: block;
}

.radio-block__input + .radio-block__label::before {
  content: "";
  position: absolute;
  top: -14px;
  left: 0;
  width: 40px;
  height: 40px;
  border: 1px solid $content-color;
  border-radius: 50%;
}

.radio-block__input:checked + .radio-block__label::after {
  content: "";
  top: -0.5px;
  left: 13px;
  position: absolute;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: $accent-color-fourth;
}

.radio-block__label:hover::before {
  border-color: $accent-color-fourth;
}

.radio-block__input:focus + .radio-block__label::before {
  border-color: $content-color-opacity-second;
}

@media (min-width: $tablet-full-width) {
  .radio-block__label {
    padding-left: 82px;
  }

  .radio-block__input + .radio-block__label::before {
    width: 52px;
    height: 52px;
  }

  .radio-block__input:checked + .radio-block__label::after {
    width: 22px;
    height: 22px;
    top: 2.5px;
    left: 16px;
  }
}
